body {
  background-color: #1b2936;
  color: #ffffff;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 {
  text-align: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Servers, .Plugins {
  margin: 20px 0;
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.Server, .ServersHeading, .Plugin, .PluginsHeader {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  padding: 1em;
}

.Server:nth-child(odd), .Plugin:nth-child(odd) {
  background-color: #414f5c;
}

.Websocket {
  width: 100%;
  display: block;
  text-align: center;
}

.Websocket-true::after {
  content: ' \2022';
  color: #04ff00;
}

.Websocket-false::after {
  content: ' \2022';
  color: #ff0000;
}
